<template>
    <div class="dashboard-wrap">
        <div>
            <h3 class="dashboard-title">Edit Hostel</h3>
            <div class="wrap">
                <div class="title">
                    Fill All Info
                </div>
                <div class="profile-container">
                    <div>
                        Edit Room Info
                         <v-form>
                            <v-container>
                                <v-row>
                                    <v-col cols="3" md="2" sm="3" style="padding : 0px 5px;">
                                        <v-subheader>Room Name</v-subheader>
                                    </v-col>
                                    <v-col cols="9" md="7" sm="6" style="padding : 0px 12px;">
                                        <v-text-field
                                        v-model="name"
                                        :error-messages="nameErrors"
                                        label="Room Name"
                                        outlined
                                        dense
                                        required
                                        @input="$v.name.$touch()"
                                        @blur="$v.name.$touch()"
                                        ></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col cols="3" md="2" sm="3" style="padding : 0px 12px;">
                                        <v-subheader>Room No.</v-subheader>
                                    </v-col>
                                    <v-col cols="9" md="7" sm="6" style="padding : 0px 12px;">
                                        <v-text-field
                                        v-model="Room_No"
                                        :error-messages="Room_NoErrors"
                                        label="Room No."
                                        outlined
                                        dense
                                        required
                                        @input="$v.Room_No.$touch()"
                                        @blur="$v.Room_No.$touch()"
                                        ></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col cols="3" md="2" sm="3" style="padding : 0px 12px;" >
                                        <v-subheader>Price</v-subheader>
                                    </v-col>
                                    <v-col cols="9" md="7" sm="6" style="padding : 0px 12px;">
                                        <v-text-field
                                        v-model="Room_Price"
                                        :error-messages="Room_PriceErrors"
                                        label="Room Price"
                                        outlined
                                        dense
                                        required
                                        @input="$v.Room_Price.$touch()"
                                        @blur="$v.Room_Price.$touch()"
                                        ></v-text-field>
                                    </v-col>
                                </v-row>
                                 <v-row>
                                    <v-col cols="3" md="2" sm="3"  style="padding : 0px 12px;">
                                        <v-subheader>Location</v-subheader>
                                    </v-col>
                                    <v-col cols="9" md="7" sm="6" style="padding : 0px 12px;">
                                        <v-text-field
                                        v-model="Location"
                                        :error-messages="LocationErrors"
                                        label="Location"
                                        required
                                        outlined
                                        dense
                                        @input="$v.Location.$touch()"
                                        @blur="$v.Location.$touch()"
                                        ></v-text-field>
                                    </v-col>
                                </v-row>
                                 
                                <v-row>
                                    <v-col cols="3" md="2" sm="3"  style="padding : 0px 12px;">
                                        <v-subheader></v-subheader>
                                    </v-col>
                                    <v-col cols="9" md="7" sm="6" style="padding : 0px 12px;">
                                        <v-btn color="primary" @click="submit">
                                            Submit
                                        </v-btn>
                                    </v-col>
                                </v-row>
                                
                            </v-container>
                        </v-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import { required } from 'vuelidate/lib/validators'

export default {
    name: "AddHostel",

    mixins: [validationMixin],

    validations: {
      name: { required },
      Location: {required},
      Room_No: {required},
      Room_Price: {required}
    },
    data : ()=> {
        return {
           name: "",
           Room_No : "",
           Location: "",
           Room_Price: ""
        }
    },
     computed: {
      nameErrors () {
        const errors = []
        if (!this.$v.name.$dirty) return errors
        !this.$v.name.required && errors.push('Name is required.')
        return errors
      },
      LocationErrors () {
        const errors = []
        if (!this.$v.Location.$dirty) return errors
        !this.$v.Location.required && errors.push('Location is required.')
        return errors
      },
      Room_PriceErrors () {
        const errors = []
        if (!this.$v.Room_Price.$dirty) return errors
        !this.$v.Room_Price.required && errors.push('Price is required.')
        return errors
      },
      Room_NoErrors () {
        const errors = []
        if (!this.$v.Room_No.$dirty) return errors
        !this.$v.Room_No.required && errors.push('Room no. is required.')
        return errors
      },
    },
    methods: {
      submit () {
        this.$v.$touch()
      }
    },
    components: {
        // Drawer
    }
}
</script>

<style scoped>
    .dashboard-wrap{
        margin: 10px 10px;
    }
    .dashboard-title{
        font-size: 24px;
        padding: 0 0 20px 0;
    }
    .wrap{
        width: 100%;
        background-color: #fff;
        border: 1px solid #ccc;
        min-height: 70vh;
        margin:20 20 10 20px;
    }
    .title{
        background-color: #ccc;
        padding: 13px;
        font-size: 12px !important;
        font-weight: 500;
    }
    .profile-container{
        width: 100%;
        min-height: 70vh;
        background-color: #fff;
        padding: 20px;
        box-shadow:  0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    }
</style>